import { Scroll } from './../interface/menu';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Disparador } from '../interface/menu';

@Component({
  selector: 'app-contactanos',
  templateUrl: './contactanos.component.html',
  styleUrls: ['./contactanos.component.css']
})
export class ContactanosComponent implements OnInit {
  public Mensaje = "";
  constructor(
    private rou : ActivatedRoute
  ) {
    
    Scroll.move();
   }

  ngOnInit() {
    if(this.rou.snapshot.paramMap.get("mensaje") !== null)
    {
      this.Mensaje = this.rou.snapshot.paramMap.get("mensaje")
    }   
  }

  ngAfterViewChecked() {
    Disparador.OnChangeComponent.emit();
  }

}
